import React , { useState , useEffect } from "react";
import { Input, Modal } from "antd";
import { EditorState, ContentState , convertToRaw } from 'draft-js';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import { Editor } from 'react-draft-wysiwyg';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import { SaveFilled } from "@ant-design/icons";
import IntlMessages from "util/IntlMessages";
import { FormattedMessage } from "react-intl";
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';


const EditPage = ({ onSavePage, onToggleModal, open, page }) => {
  
    const [name, setName] = useState('')
    const [editorState, setEditorState] = useState(null)
    const [content, setContent] = useState(null)

    let contentState;
    let editorState1;
    if(content){
      const blocksFromHtml = htmlToDraft(content);
      const { contentBlocks, entityMap } = blocksFromHtml;
      contentState = ContentState.createFromBlockArray(contentBlocks, entityMap);
      editorState1 = EditorState.createWithContent(contentState);
    }


    const onEditorStateChange = (editorState) => {
      setEditorState(editorState)
    };
    

    useEffect(() => {
      if(Object.keys(page).length !== 0){
        setName(page.name)
        setContent(page.content)
      }
    }, [page])

    return (
      <Modal
        width={'80%'}
        okText={<SaveFilled />}
        cancelText={ <IntlMessages id="modal.cancelSave"/> }
        title={<IntlMessages id="city.saveCity"/>}
        toggle={onToggleModal} 
        visible={open}
        closable={false}
        onOk={() => {
          if (name === '' || content === '')
            return;
          onToggleModal("editPageState");
          let body = content
          if(editorState){
            body = draftToHtml(convertToRaw(editorState.getCurrentContent()))
          }
          onSavePage({ id:page.id , name , content:body });
          setName(name)
          setContent(null)
        }}
        onCancel={()=> {
          onToggleModal('editPageState')
          setName(page.name)
          setContent(null)
        }}>

        <div  className="gx-modal-box-row">
          <div className="gx-modal-box-form-item">
            <div className="gx-form-group">
              <FormattedMessage id="columns.name" defaultMessage="name">
              {
                placeholder => (
                <Input
                  required
                  placeholder={placeholder}
                  onChange={(event) => setName(event.target.value)}
                  value={name}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>
            <div className="gx-form-group">
              <Editor
                defaultEditorState={editorState1}
                textAlignment="right"
                toolbarClassName="toolbarClassName"
                wrapperClassName="wrapperClassName"
                editorClassName="editorClassName"
                onEditorStateChange={onEditorStateChange}
              />
            </div>
          </div>
        </div>
      </Modal>
    );
}

export default React.memo(EditPage);
